var installed = false;
var installedCSRF = {token: null};

export function ensureInstalled() {
  if (installed) {
    return;
  }

  // Load UIKit and tell it to use its 'Icons' plugin
  const UIKit = require('uikit');
  const Icons = require('uikit/dist/js/uikit-icons');
  UIKit.use(Icons);

  const dayjs = require('dayjs');
  const relativetime = require('dayjs/plugin/relativeTime');
  dayjs.extend(relativetime);

  var req = new XMLHttpRequest();
  req.addEventListener('load', function() {
    console.log('req onload headers', req.getResponseHeader('x-csrf-token'));
    installedCSRF.token = req.getResponseHeader('x-csrf-token');

  });
  req.open('HEAD', document.location, true);
  req.send(null);

  installed = true;
}

export function getInstalledCSRF() {
  return installedCSRF.token;
}